.react-terminal-wrapper{width:100%;height:100%;background:#252a33;color:#eee;font-size:18px;font-family:"Fira Mono",Consolas,Menlo,Monaco,"Courier New",Courier,monospace;border-radius:4px;padding:75px 45px 35px;position:relative;-webkit-box-sizing:border-box;box-sizing:border-box}.react-terminal{height:100%;overflow:auto;display:flex;flex-direction:column}.react-terminal-wrapper.react-terminal-light{background:#ddd;color:#1a1e24}.react-terminal-wrapper:before{content:"";position:absolute;top:15px;left:15px;display:inline-block;width:15px;height:15px;border-radius:50%}.react-terminal-wrapper:after{content:attr(data-terminal-name);position:absolute;color:#a2a2a2;top:5px;left:0;width:100%;text-align:center}.react-terminal-line{white-space:pre}.react-terminal-line:before{content:"";display:inline-block;vertical-align:middle;color:#a2a2a2}.react-terminal-input:before{margin-right:.75em;content:"$"}.react-terminal-input[data-terminal-prompt]:before{content:attr(data-terminal-prompt)}.react-terminal-wrapper:focus-within .react-terminal-active-input:after{content:"▋";font-family:monospace;margin-left:.2em;-webkit-animation:blink 1s infinite;animation:blink 1s infinite}@-webkit-keyframes blink{50%{opacity:0}}@keyframes blink{50%{opacity:0}}.terminal-hidden-input{position:fixed;left:-1000px}